"use strict";
const index_1 = require("./index");
const chai_1 = require("chai");
const { deepEqual: same } = chai_1.assert;
describe("range", () => {
    describe("number", () => {
        it("0", () => {
            const { done } = index_1.range(0).next();
            chai_1.assert.equal(done, true);
        });
        it("[0,2), with index", () => {
            const gen = index_1.range(2);
            let n1 = gen.next();
            chai_1.assert.deepEqual(n1, {
                done: false,
                value: 0,
                index: 0,
            });
            let n2 = gen.next();
            chai_1.assert.deepEqual(n2, {
                done: false,
                value: 1,
                index: 1,
            });
            let n3 = gen.next();
            chai_1.assert.deepEqual(n3, {
                done: true,
            });
        });
    });
    describe("value collection", () => {
        it("array", () => {
            same(index_1.range(3).array(), [0, 1, 2]);
        });
        it("dictionary", () => {
            same(index_1.range(3).dict(), {
                '0': 0,
                '1': 1,
                '2': 2,
            });
        });
    });
    // todo reindex
    describe("array", () => {
        it("[]", () => {
            const { done } = index_1.range([]).next();
            chai_1.assert.equal(done, true);
        });
        it("[42], with index", () => {
            let gen = index_1.range([42]);
            let n1 = gen.next();
            chai_1.assert.deepEqual(n1, {
                done: false,
                value: 42,
                index: 0,
            });
            let n2 = gen.next();
            chai_1.assert.deepEqual(n2, {
                done: true,
            });
        });
    });
});
